
import { SEC20100121_REQ_BODY, SEC20100121_RES_BODY } from '@/shared/type/SEC20100121.type'
import { SEC20100112_REQ_BODY, SEC20100112_RES_BODY } from '@/shared/type/SEC20100112.type'
import { defineComponent } from 'vue'
import Timer from '@/shared/components/Timer/Timer.vue'
import { SEC20100111_REQ_BODY } from '@/shared/type/SEC20100111.type'

export default defineComponent({
    name: "sec201001sc00Vue",
    components: { Timer },
    data() {
        return {
            login: {
                employeeNo: '',
                password: '',
                isRemember: false
            },
            otp: {
                authTransactionDate: "",
                authTransactionSeqNo: 0,
            } as SEC20100121_RES_BODY,
            mode: 'login' as 'login' | 'otp',
            btnLabel: 'Send' as 'Send' | 'Resend',
            preLogin: {
                employeeNo: '',
                employeeName: '',
                emailAddress: '',
                branchCode: '',
                branchName: ''
            } as SEC20100112_RES_BODY,
            isDisableOTP: true,
            isCountDown: false,
            isInvalid: false,
            otpCode: ''
        }
    },
    mounted(){
        this.login.employeeNo = localStorage.getItem('employeeNo') as string
        this.login.isRemember = localStorage.getItem('employeeNo') == null ? false : true 
    },
    methods: {
        async onClickSignIn() {
            if(this.login.employeeNo && this.login.password){
                const reqBody: SEC20100112_REQ_BODY = {
                    employeeNo: this.login.employeeNo,
                    password: this.login.password
                }
                const res = await this.$auth.onLogin(reqBody)
                if(res){
                    if(res.header.result){
                        if(this.login.isRemember){
                            localStorage.setItem('employeeNo', this.login.employeeNo)
                        } else {
                            localStorage.removeItem('employeeNo')
                        }
                        this.mode = 'otp'
                        this.preLogin = res.body
                    }
                }
            }
        },

        async onClickSendOTP() {
            this.otpCode = ''
            const reqBody: SEC20100121_REQ_BODY = {
                employeeNo: this.preLogin.employeeNo,
                email: this.preLogin.emailAddress
            }
            const res = await this.$auth.onSendOTP(reqBody)

            if(res){
                if(res.header.result){
                    this.isCountDown = true
                    this.otp = res.body
                    this.isDisableOTP = false
                }
            }
        },

        async onClickConfirm() {
            this.isInvalid = false
            if(this.otpCode !== ''){
                const reqBody : SEC20100111_REQ_BODY = {
                    employeeNo: this.preLogin.employeeNo,
                    emailAddress: this.preLogin.emailAddress,
                    authTransactionDate: this.otp.authTransactionDate,
                    authTransactionSeqNo: this.otp.authTransactionSeqNo,
                    authenticationCode: this.otpCode
                }

                await this.$auth.onVerifyOtp(reqBody)
            } else {
                this.isInvalid = true
            }
        },

        handleStopCountdown() {
            this.isDisableOTP = true
            this.isCountDown = false
            this.btnLabel = 'Resend'
        },
        onClickBack(){
            this.mode = 'login'
            this.btnLabel = 'Send'
            this.isDisableOTP = true
            this.otpCode = ''
            this.isInvalid = false
            this.isCountDown = false
        }
    }
})
