
import { defineComponent } from 'vue';

export default defineComponent({
    name: 'Timer',
    emits: ['stop'],
    props: {
        minutes: {
            type: Number,
            default: 0,
            required: true
        },
        seconds: {
            type: Number,
            default: 0,
            required: true
        },
        isCountingDown: {
            type: Boolean,
            default: false
        }
    },
    data(){
        return {
            intervalId: null as null | number,
            countdownValue: '0:00'
        }
    },
    watch: {
        isCountingDown(isCountDown: boolean) {
            if(isCountDown){
                this.startCountdown()
            } else {
                this.stopCountdown()
            }
        }
    },
    mounted(){
        this.countdownValue = `${this.minutes < 10 ? '0' + this.minutes : this.minutes}:${this.seconds < 10 ? '0' + this.seconds : this.seconds}`
    },
    methods: {
        startCountdown() {
            let totalSeconds = this.minutes * 60 + this.seconds;

            this.intervalId = setInterval(() => {
                if (totalSeconds <= 0) {
                    clearInterval(this.intervalId!);
                    this.$emit('stop')
                } else {
                    totalSeconds--;
                    const currentMinutes = Math.floor(totalSeconds / 60);
                    const currentSeconds = totalSeconds % 60;
                    this.countdownValue = `${currentMinutes < 10 ? '0' + currentMinutes : currentMinutes}:${currentSeconds < 10 ? '0' + currentSeconds : currentSeconds}`
                }
            }, 1000);
        },

        stopCountdown() {
            if (this.intervalId) {
                this.countdownValue = `${this.minutes < 10 ? '0' + this.minutes : this.minutes}:${this.seconds < 10 ? '0' + this.seconds : this.seconds}`;
                clearInterval(this.intervalId);
                this.intervalId = null;
                this.$emit('stop')
            }
        }
    },
    unmounted(){
        this.stopCountdown()
    }
})
